<template>
  <div class="subject-view has-header">
    <banner :title="bannerTitle"></banner>
    <div class="subject-card">
      <h1 class="title">{{subject.title}}</h1>
      <div class="subject-info">
        <div class="right">
          <a href="#">
            <img v-if="subject.images" :src="subject.images.large" alt="cover">
          </a>
        </div>
        <div class="left" v-if="subject.rating">
          <rating :rating="subject.rating">
            <span slot="ratingsCount">{{subject.rating.numRaters}}</span>
          </rating>
          <template v-if="subject.genres && subjectMeta">
            <p class="meta">{{subjectMeta}}</p>
            <a href="#" class="open-app">
              App
            </a>
          </template>
          <template v-if="subject.author && subjectMeta">
            <p class="meta">{{subjectMeta}}</p>
            <a href="#" class="buy">
              
            </a>
          </template>
        </div>
      </div>
      <div v-if="subject.author" class="vendors-link">
        <a class="link">
          
          <span class="info">
             66.00
          </span>
        </a>
      </div>
      <subject-mark></subject-mark>
      <div class="subject-intro">
        <h2>{{subject.title}}</h2>
        <p>
          <template v-if="subject.summary">
            {{isExpand ? summary : subject.summary}}
          </template>
          <a href="javascript:;" v-show="isExpand" @click="expand">()</a>
        </p>
      </div>
      <div class="genres">
        <h2></h2>
        <template v-if="genres">
          <tags :items="genres"></tags>
        </template>
      </div>
      <div class="subject-pics">
        <h2>{{subject.title}}</h2>
        <ul v-if="subject.images">
          <li class="pic">
            <a href="#">
              <img :src="subject.images.large">
            </a>
          </li>
          <li class="pic">
            <a href="#">
              <img :src="subject.images.large">
            </a>
          </li>
          <li class="pic">
            <a href="#">
              <img :src="subject.images.large">
            </a>
          </li>
        </ul>
      </div>
    </div>
    <div class="subject-comments">
      <h2>{{subject.title}}</h2>
      <div class="content-list">
        <card mold="comment" v-for="item in items" :key="item"></card>
        <a href="javascript:;" class="list-link"></a>
      </div>
    </div>
    <div class="ad">
      <banner isAd="true" :adImg="adImgUrl" noContent></banner>
    </div>
    <div class="subject-question">
      <h2>{{subject.title}}</h2>
      <list :items="questions"></list>
      <a href="javascript:;" class="list-link"></a>
    </div>
    <scroller title="" type="onlyString" :item="movieTags"></scroller>
    <download-app></download-app>
  </div>
</template>

<script>
  import { mapState, mapGetters } from 'vuex'
  import Banner from '../components/Banner.vue'
  import Card from '../components/Card.vue'
  import Scroller from '../components/Scroller.vue'
  import DownloadApp from '../components/DownloadApp.vue'
  import Tags from '../components/Tags.vue'
  import Rating from '../components/Rating.vue'
  import List from '../components/List.vue'
  import subjectMark from '../components/SubjectMark.vue'

  export default {
    name: 'subject-view',
    components: { Banner, Card, List, Scroller, DownloadApp, Tags, Rating, subjectMark},
    data () {
      return {
        isExpand: true,
        items: new Array(5)
      }
    },
    computed: {
      ...mapState({
        bannerTitle: state => state.subject.bannerTitle,
        subject: state => state.subject.subject,
        adImgUrl:state => state.subject.adImgUrl,
        questions: state => state.subject.questions,
        movieTags: state => state.movie.movieTags
      }),
      ...mapGetters({
        subjectMeta: 'subjectMeta',
        summary: 'summary',
        genres: 'genres'
      })
    },
    methods: {
      expand: function (event) {
        this.isExpand = false
      }
    },
    beforeMount () {
      const id = this.$route.params.id
      const classify = this.$route.params.classify
      this.$store.dispatch({
        type: 'getSingleSubject',
        id: id,
        classify: classify
      })
    }
  };
</script>

<style lang="scss" rel="stylesheet/scss" scoped>


  .subject-card {
    padding: 0 1.8rem;

    h1 {
      margin: 2rem 0 0;
    }
  }

  .subject-info {
    overflow: hidden;
    margin-bottom: 3rem;

    .right {
      float: right;

      img {
        display: block;
        width: 100%;
        max-width: 10rem;
      }
    }

    .left {
      margin-right: 10rem;

      .meta {
        margin-top: 1.5rem;
        padding-right: 2.4rem;
        line-height: 1.6;
        font-size: 1.4rem;
        color: #494949;
      }

      .buy {
        display: inline-block;
        height: 2.4rem;
        padding: 0 0.6rem;
        line-height: 2.4rem;
        text-align: center;
        font-size: 1.3rem;
        color: #E76648;
        border: 0.1rem solid #E76648;
        border-radius: 0.3rem;
      }

      .open-app {
        display: block;
        margin-top: 1rem;
        line-height: 1;
        font-size: 1.4rem;
        color: #42bd56;
      }
    }
  }

  .vendors-link {
    position: relative;
    margin: 1.5rem 0;
    padding: 1rem 1.9rem 1rem 0;
    line-height: 2.4rem;
    font-size: 1.5rem;
    overflow: auto;
    box-sizing: border-box;

    .link {
      display: inline-block;
      width: 100%;
      position: relative;
    }

    .info {
      position: absolute;
      top: 0;
      right: 0;
      display: inline-block;
      color: #ccc;
      font-size: 1.4rem;
    }

    &::before {
      left: 0;
      top: 0;
      width: 100%;
      height: 1px;
      background: #E8E8E8;
      content: '';
      position: absolute;
    }

    &::after {
      left: 0;
      bottom: 0;
      width: 100%;
      height: 1px;
      background: #E8E8E8;
      content: '';
      position: absolute;
    }
  }

  .subject-intro, .genres, .subject-pics, .subject-comments,
  .ad, .subject-question {
    margin-bottom: 3rem;

  }

  h2 {
    margin: 0 0 1.5rem;
    font-size: 1.5rem;
    color: #aaa;
  }

  .subject-intro {
    p {
      //height: 10rem;
      font-size: 1.5rem;
      color: #494949;
      //overflow: hidden;
    }

    a {
      //float: right;
      color: #42bd56;
    }
  }

  .tags {
    li {
      display: inline-block;
      margin: 1rem 1rem 0 0;
      font-size: 1.5rem;
    }

    a {
      display: block;
      padding: 0 1.2rem;
      line-height: 2.8rem;
      font-size: 1.5rem;
      border-radius: 2.8rem;
      text-align: center;
      color: #494949;
      background: #f5f5f5;
    }
  }

  .subject-pics {
    ul {
      margin-right: -1.8rem;
      overflow-x: auto;
      white-space: nowrap;
    }

    li {
      height: 12rem;
      overflow: hidden;
      display: inline-block;
    }

    img {
      width: 18rem;
    }
  }

  .subject-comments h2,  .subject-question {
    padding: 0 1.8rem;
  }

  .subject-comments, .subject-question {
    .list-link {
      display: block;
      padding: 1.5rem 0;
      font-size: 1.6rem;
      line-height: 1.8rem;
      text-align: center;
      color: #42bd56;
    }
  }

  .ad {
    margin: 3rem 1.8rem;
    margin-top: -2rem;
  }


</style>



